import axios from 'axios';
import dompurify from 'dompurify';

function searchResultsHTML(companies) {
  return companies.map(company => {
    return `
        <a href="/companies/${company.slug}" class="search__result">
          <strong>${company.name}</strong>
        </a>
    `;
  }).join('');
};

function typeAhead(search) {
  if(!search) return;

  const searchInput = search.querySelector('input[name="search"]');
  const searchResults = search.querySelector('.search__results');

  searchInput.addEventListener('input', function() {
    // If there is no value - quit it!
    if(!this.value) {
      searchResults.style.display = 'none';
      return; // stop!
    };
    //Show the search results!
    searchResults.style.display = 'block';

    axios.get(`/api/search?q=${this.value}`).then(res => {
      if(res.data.length) {
        searchResults.innerHTML = dompurify.sanitize(searchResultsHTML(res.data));
        return;
      }
      // tell them nothing came back
      searchResults.innerHTML = dompurify.sanitize(`<div class="search__result">  ${this.value}  !</div>`);
    }).catch(err => {
      console.log(err);
    });
  });

  // Handle Keyboard Inputs
  searchInput.addEventListener('keyup', (e) => {
    // If they aren't pressing up, down or enter - who cares?!
    if(![38, 40, 13].includes(e.keyCode)) {
      return; // skip it
    }

    const activeClass = 'search__result--active';
    const current = search.querySelector(`.${activeClass}`);
    const items = search.querySelectorAll('.search__result');
    let next;

    if (e.keyCode === 40 && current) {
      next = current.nextElementSibling || items[0];
    } else if (e.keyCode === 40) {
      next = items[0];
    } else if (e.keyCode === 38 && current) {
      next = current.previousElementSibling || items[items.length - 1];
    } else if (e.keyCode === 38) {
      next = items[items.length - 1];
    } else if (e.keyCode === 13 && current.href) {
      window.location = current.href;
      return;
    }

    if (current) {
      current.classList.remove(activeClass);
    }

    next.classList.add(activeClass);

  });
};

export default typeAhead;



